#intro {
  height: calc(100vh - 4rem);
  width: 100vw;
  max-width: 75rem;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}

.bg {
  position: absolute;
  top: -2rem;
  right: 0;
  z-index: -1;
  height: 40rem;
}
.introContent {
  height: 100vh;
  width: 100vh;
  padding: 2rem;
  font-size: 3rem;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hello {
  font-size: 1.75rem;
  font-weight: 100;
}
.introName {
  color: yellow;
}
.introPara {
  font-size: medium;
  font-weight: 300;
  letter-spacing: 1px;
}
.btn {
  background: linear-gradient(
    90deg,
    rgba(14, 193, 207, 1) 0%,
    rgba(89, 118, 185, 1) 41%,
    rgba(50, 15, 97, 1) 89%
  );
  color: white;
  display: flex;
  border: none;
  margin: 1rem 0.5rem;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  border-radius: 2rem;
}
.btnImg {
  object-fit: cover;
  margin: 0 0.25rem;
  height: 1rem;
}
.buttons {
  display: flex;
}

@media screen and (max-width: 840px) {
  .bg {
    right: -10vw;
  }
  .introContent {
    font-size: 10vw;
  }
  .hello {
    font-size: 4.5vw;
  }
}
@media screen and (max-width: 480px) {
  .bg {
    right: -20vw;
  }
}
